import React, {useEffect, useState} from "react";
import { sendMessage } from "../../pages/api/sendMessage";
import { randomCode } from "../util/common";
import Modal from "react-modal";
type sendParam = {
    code : string,
    cell : string
}
interface CellProps {
    cell : string,
    setCode : React.Dispatch<React.SetStateAction<string>>,
    isCell : boolean
}
const SendButton = ({cell, setCode, isCell} : CellProps) => {
    const [sendFlag, setSendFlag] = useState<boolean>(false);
    const [count, setCount] = useState<number>(0);
    const [show, setShow] = useState<boolean>(false);
    const customStyles = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
        },
        content: {
            left: "50%",
            margin: "auto",
            width: "350px",
            height: "200px",
            padding: "0",
            overflow: "hidden",
            transform : "translate(-50%,0)"
        },
    };
    const onClickHandler = (e : React.MouseEvent<HTMLElement>) => {
         sendMessage(cell)
            .then((res : any) => {
                setCode(res.code);
                setCount(count+1);
                setShow(true);
                setSendFlag(false);
                setTimeout(() => {
                    setSendFlag(true);
                }, 10000);
            }).catch((error) => {
                alert(error.msg);
         })
    }
    useEffect(() => {
        if(isCell){
            setSendFlag(true);
        }else {
            setSendFlag(false);
        }
    }, [isCell]);
    return (
        <div>
            <button className={sendFlag ? 'button_on' : 'button_off'} onClick={onClickHandler} disabled={!sendFlag}>{ count === 0 ? ' ' : ' '}</button>
            {
                  show &&
                <Modal isOpen={show} style={customStyles}>
                    <div className='border-b bg-[#f6fbf7] border-b-[#157b4b] font-bold'>
                        <div className='flex justify-center py-2'>
                              
                        </div>
                    </div>
                    <div className='flex justify-center mt-8'>
                        <h2>
                              .
                        </h2>
                    </div>
                    <div className='fixed bottom-0 w-full flex justify-center py-2'>
                        <button id={'confirm'} className='button_on' onClick={(e:React.MouseEvent<HTMLElement>)=>setShow(false)}></button>
                    </div>
                </Modal>
            }
        </div>
    )
}
export default SendButton;