import React, {useState} from "react";
import CloseIcon from "../../../assets/images/btn_close_24px.svg";
interface ConsntProps{
    sel : boolean,
    setSel : React.Dispatch<React.SetStateAction<boolean>>
}
const ConsntText = ({sel, setSel} : ConsntProps) => {
    const [conShow, setConShow] = useState(sel);
    const onClickHandler = (e : React.MouseEvent<HTMLElement>) => {
        setConShow(!conShow);
        setTimeout(() => {
            setSel(!sel);
        }, 500);
    }
    return(
        <div>
            <div className='fixed inset-0 w-full z-10 '>
                <div className={conShow ? 'absolute bg-white bottom-0 left-0 w-screen h-full bg-white animate-[left-side-on_600ms_ease-in-out] overflow-y-auto pb-8' : 'absolute bottom-0 h-full left-0  w-screen bg-white animate-[left-side-out_600ms_ease-in-out]'}>
                    <div className='flex items-center mb-8 text-[14px] h-[45px] bg-[#f6fbf7] border-b-[2px] border-b-[#157b4b] title_text fixed top-0 w-full'>
                        <div className="flex w-full justify-center">() ,   </div>
                        <div className='mr-2'> <button onClick={(e) => onClickHandler(e)}><img src={CloseIcon}/></button></div>
                    </div>
                    <div className='text-[11px] mx-8 justify-center flex-col pt-[70px]'>
                        <div className='bold_12'>
                                             .
                        </div>
                        <div className='pt-4'>
                            <h2 className='pb-2 bold_12'>1.    </h2>
                            <h2>
                                    , ,   
                                <br/>
                                (    )
                            </h2>
                            <h2 className='mt-2'>
                                       
                            </h2>
                        </div>
                        <div className='pt-4'>
                            <h2 className='pb-2 bold_12'>2.  </h2>
                            <h2 className='font-bold text-[11px]'>
                                1) 
                            </h2>
                            <h2 className='mt-1'>
                                 , , 
                            </h2>
                            <h2 className='font-bold text-[11px] mt-2'>
                                2) ()
                            </h2>
                            <div className='flex-col ml-1'>
                                <h2 className='mt-1'>
                                      : , (CI,D.I), , ,  , 
                                </h2>
                                <h2 className='mt-1'>
                                      : (,, ), 
                                </h2>
                                <h2 className='mt-1'>
                                      :   
                                </h2>
                            </div>
                            <h2 className='font-bold text-[11px] mt-2'>
                                3) 
                            </h2>
                            <h2 className='pt-1'>
                                     (,  ),
                                 
                            </h2>
                        </div>
                        <div className='pt-4'>
                            <h2 className='pb-2 bold_12'>3. </h2>
                            <h2 className='pt-1'>
                                  1
                            </h2>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default ConsntText;