import React, {useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import { useDispatch } from "react-redux";
import {userPhone} from "../../pages/reducers/userInfo";

interface Props{
    code : string,
    inputCode : string,
    isCode : boolean,
    cell : string,
    check : boolean
}
const ConfirmButton = ({code, inputCode, isCode, cell, check} : Props) => {
    const movePage = useNavigate();
    const dispatch = useDispatch();
    const onClickHandler = (e : React.MouseEvent<HTMLElement>) => {
        if(check){
            if(code !== inputCode || inputCode === ''){
                alert('  .');
            }else{
                movePage('/register');
                dispatch(userPhone(cell));
            }
        }else {
            alert(" ,  .");
        }
    }
    useEffect(() => {

    }, [inputCode]);
    return (
        <div>
            <button className={isCode ? 'button_on' : 'button_off'} onClick={onClickHandler} disabled={!isCode}></button>
        </div>
    )
}
export default ConfirmButton;