import React, {useEffect, useState} from "react";
type InfoProps = {
    bpk : string,
    cName : string,
    cCell : string,
    cJumin : string,
    cMail : string,
    cPost : string,
    cAddr1 : string,
    cAddr2 : string,
    cJob : string,
}
const SelectBpk = (props : {info : InfoProps, setInfo : React.Dispatch<React.SetStateAction<InfoProps>>} ) => {
    const [bpk, setBpk] = useState<string>('')
    useEffect(() => {
        props.setInfo((prevState) => {
            return { ...prevState, bpk: bpk }
        });
    }, [bpk]);
    return(
        <div className='pt-8 flex flex-col w-full'>
            <div className='sub_title pb-2 pl-4 flex justify-center '>
                
            </div>
            <div className='sub_title pb-4 pl-4 flex justify-center '>
                <select  className='input ml-2' onChange={(e : React.ChangeEvent<HTMLSelectElement>)=> setBpk(e.target.value)}>
                    <option defaultValue={'none'}>
                        
                    </option>
                    <option value={'onna'}>
                        
                    </option>
                    <option value={'enuri'}>
                        
                    </option>
                    <option value={'barico'}>
                        
                    </option>
                </select>
            </div>
        </div>
    )
}
export default SelectBpk;